</template>

<script>
	import { TweenMax, TimelineLite, Power2} from 'gsap'

	import IconBase from './icon/IconBase.vue'
	import IconImac from './icon/IconImac.vue'
	import IconTable from './icon/IconTable.vue'
	import IconBoard from './icon/IconBoard.vue'
	import IconLamp from './icon/IconLamp.vue'
	import IconCactus from './icon/IconCactus.vue'
	import IconHeadMan from './icon/IconHeadMan.vue'
	import IconSmoke from './icon/IconSmoke.vue'
	import IconMoney from './icon/IconMoney.vue'
	import iconTea from './icon/iconTea.vue'
	import IconFactory from './icon/IconFactory.vue'
	import IconCloud from './icon/IconCloud.vue'
	import IconPhone from './icon/IconPhone.vue'
	import IconAccounting from './icon/IconAccounting'
	import IconFile from './icon/IconFile.vue'
	import IconArrow from './icon/IconArrow.vue'
	import IconClose from './icon/IconClose.vue'
	import City from './City.vue'
	import IconTap from './icon/IconTap'
	import iconTelephone from './icon/iconTelephone.vue'

	import RadioAnim from './iconAmination/RadioAnim.vue'

	export default {

		data: function() {
			return {
				isHidden: true,
				isHiddenLamp: false,
				lamp: undefined,
				money: undefined,
				tea: undefined,
				head: undefined,
				selected: 0,
				services: false,

				widthDevice:0,
				heightDevice:0,

				centerX:0,
				leftGran:0,
				rightGran:0,

				cactusKill:false,

				preloader: true,

				form: false,

				x:0,
				y:0,

			}
		},
		mounted() {
			this.lamp = this.$refs.lamp;
			this.head = this.$refs.head;
			this.money = this.$refs.money;
			this.tea = this.$refs.tea;
			this.poyavlenie();

			this.cardAnimStartAll();
			this.cardAnimPauseAll();
		},
		created() {

			window.addEventListener('keydown', this.keyUnvisibleServices)

			window.addEventListener('resize', this.windowSize)
			this.windowSize();
			document.body.scrollTop = document.documentElement.scrollTop = 0;
		},
		destroyed() {
			window.removeEventListener('resize', this.windowSize)
		},
		name: 'DeveloperRoom',
		components:{
			IconBase,
			IconImac,
			IconTable,
			IconBoard,
			IconLamp,
			IconCactus,
			IconHeadMan,
			IconSmoke,
			IconMoney,
			iconTea,
			IconFactory,
			IconCloud,
			IconPhone,
			IconAccounting,
			IconFile,
			IconArrow,
			IconClose,
			IconTap,
			City,
			iconTelephone,

			RadioAnim
		},
		methods: {
			killSmoke(){
				this.isHidden = false;
			},
			visibleLamp() {
				this.isHiddenLamp = true;
			},
			unvisible() {
				this.isHiddenLamp = false;
			},
			crashLamp() {
				this.$refs.lamp.crash();
			},
			visibleMoney(){
				this.money.hello();
			},
			unvisibleMoney() {
				this.money.bay();
			},
			visibleForm() {
				this.form = true;
			},
			unvisibleForm() {
				this.form = false
			},
			crashTea() {
				this.killSmoke();
				this.tea.crash();
			},
			poyavlenie() {

				var tl = new TimelineLite();
				var Y = window.innerHeight * 700 / 969;
				var X = window.innerHeight * -200 / 969;

				tl.to('#icon__city', 0.75, { ease: Power2.easeIn, transformOrigin:"center", scale:1, y:0, x:0});
				tl.to('#icon__city', 2, { ease: Power2.easeIn, transformOrigin:"center", scale:60, y:-Y, x:-X});
				tl.to('#icon__city', 0.25, { ease: Power2.easeIn, transformOrigin:"center", scale:60, y:-Y, x:-X});
				tl.to('#icon__preloader', 0.4, {ease: Power2.easeIn, opacity:0}, '-=0.6');

				setTimeout(this.start, 2500);
				setTimeout(function () {
					this.$refs.icon__preloader.style.pointerEvents = "none";
					tl.kill();
					this.preloader = false;
				}.bind(this), 3000)
			},
			strelkaPlus() {
				this.cardAnimPauseAll();
				this.plus()
				this.cardAnimPlay();
			},
			strelkaMinus() {
				this.cardAnimPauseAll();
				this.minus()
				this.cardAnimPlay();
			},
			plus() {
				if (this.selected < 5) {
					this.selected += 1;
				}
				else {
					this.selected = 1;
				}
			},
			minus() {
				if (this.selected > 1) {
					this.selected -= 1;
				}
				else {
					this.selected = 5;
				}
			},
			visibleServices() {
				this.services = true;
				this.stop();
				this.cardAnimPlay();
			},
			keyUnvisibleServices(e) {
				if (e.key == 'Escape') {
					this.unvisibleServices();
				}
			},
			unvisibleServices() {
				this.services = false;
				this.start();
				this.cardAnimPauseAll();
				this.cardAnimStopAll();
			},
			cardMove(e) {
				if (e.pointerType === "touch") {
					this.cardAnimPause();

					if (e.center.x < this.leftGran) {
						TweenMax.to('#bounce--card', 0.8, {opacity: 0.25});
					}
					else if (e.center.x > this.rightGran) {
						TweenMax.to('#bounce--card', 0.8, {opacity: 0.25});
					}
					else {
						TweenMax.to('#bounce--card', 0.25, {opacity: 1});
					}

					var X = e.center.x - this.centerX;

					TweenMax.to('#bounce--card', 0.3, {x: X, transformOrigin: "end", rotation: X / 20, y: (X / 50) * (X / 50)});
				}
			},
			cardStart(e) {
				if (e.pointerType === "touch") {

					if (e.velocityX > 0.4) {
						this.cardExitRight();
					}
					else if (e.velocityX < -0.4) {
						this.cardExitLeft();
					}
					else {

						if (e.center.x > this.rightGran) {
							this.cardExitRight();
						}
						else if (e.center.x < this.leftGran) {
							this.cardExitLeft();
						}
						else {
							this.cardAnimPlay();
							TweenMax.to('#bounce--card', 0.75, {x: 0, y: 0, transformOrigin: "end", rotation: 0});
							TweenMax.to('#bounce--card', 0.25, {opacity: 1});
						}

					}

				}
				else {
					this.cardAnimPlay();
					TweenMax.to('#bounce--card', 0.75, {x: 0, y: 0, transformOrigin: "end", rotation: 0});
					TweenMax.to('#bounce--card', 0.25, {opacity: 1});
				}
			},
			cardExitRight() {
				this.cardAnimPauseAll();

				var tl = new TimelineLite();

				tl.to('#bounce--card', 0.4, {x: (this.rightGran - this.centerX) * 10, opacity:0 });
				tl.set('#bounce--card',{x: (this.rightGran - this.centerX) * 10 * (-1), opacity:1 })
				tl.to('#bounce--card', 0.2, {x: 0, y: 0, transformOrigin: "end", rotation: 0});

				setTimeout(this.plus, 50);
				setTimeout(this.cardAnimPlay, 700);
			},
			cardExitLeft() {

				this.cardAnimPauseAll();

				var tl = new TimelineLite();

				tl.to('#bounce--card', 0.4, {x: (this.rightGran - this.centerX) * 10 * (-1), opacity:0 });
				tl.set('#bounce--card',{x: (this.rightGran - this.centerX) * 10, opacity:1})
				tl.to('#bounce--card', 0.2, {x: 0, y: 0, transformOrigin: "end", rotation: 0});

				setTimeout(this.minus, 50);
				setTimeout(this.cardAnimPlay, 700);
			},
			cardAnimStartAll() {
				this.$refs.iconFactory.start();
				this.$refs.iconCloud.start();
				this.$refs.iconPhone.start();
				this.$refs.iconAcc.start();
				this.$refs.iconFile.start();
				this.$refs.iconTap.start();
			},
			cardAnimStopAll() {
				this.$refs.iconFactory.stop();
				this.$refs.iconCloud.stop();
				this.$refs.iconPhone.stop();
				this.$refs.iconAcc.stop();
				this.$refs.iconFile.stop();
				this.$refs.iconTap.stop();
			},
			cardAnimPause() {
				if (this.selected === 1) {
					this.$refs.iconFactory.pause();
				}
				else if (this.selected === 2) {
					this.$refs.iconCloud.pause();
				}
				else if (this.selected === 3) {
					this.$refs.iconPhone.pause();
				}
				else if (this.selected === 4) {
					this.$refs.iconAcc.pause();
				}
				else if (this.selected === 5) {
					this.$refs.iconFile.pause();
				}
			},
			cardAnimPauseAll() {
				this.$refs.iconFactory.pause();
				this.$refs.iconCloud.pause();
				this.$refs.iconPhone.pause();
				this.$refs.iconAcc.pause();
				this.$refs.iconFile.pause();
				this.$refs.iconTap.pause();
			},
			cardAnimPlay() {
				if (this.selected === 0) {
					this.$refs.iconTap.play();
				}
				if (this.selected === 1) {
					this.$refs.iconFactory.play();
				}
				else if (this.selected === 2) {
					this.$refs.iconCloud.play();
				}
				else if (this.selected === 3) {
					this.$refs.iconPhone.play();
				}
				else if (this.selected === 4) {
					this.$refs.iconAcc.play();
				}
				else if (this.selected === 5) {
					this.$refs.iconFile.play();
				}
			},
			windowSize() {
				this.widthDevice = window.innerWidth;
				this.heightDevice = window.innerHeight;

				this.centerX = this.widthDevice/2;
				this.leftGran = this.widthDevice * 0.05;
				this.rightGran = this.widthDevice - this.leftGran;
			},
			stop() {
				this.head.stop();
				this.lamp.stop();
			},
			start() {
				this.lamp.start();
				this.head.start();
			},
		},
		computed: {
			boolLamp() {
				return this.isHiddenLamp;
			},
		},
	}
</script>

